import Vue from 'vue'
import App from './App.vue'
import store from './store'
import router from './router'
import Axios from 'axios'
import VueClipboard from 'vue-clipboard2'

import AxiosPlugin from 'vue-axios-cors';

Vue.use(AxiosPlugin)

import {BootstrapVue, IconsPlugin} from 'bootstrap-vue'

import device from "vue-device-detector"
Vue.use(device)


import Select2 from 'v-select2-component';

Vue.component('Select2', Select2);



VueClipboard.config.autoSetContainer = true // add this line
Vue.use(VueClipboard)

import VueSweetalert2 from 'vue-sweetalert2';
// If you don't need the styles, do not connect
import 'sweetalert2/dist/sweetalert2.min.css';
Vue.use(VueSweetalert2)

// Install BootstrapVue
Vue.use(BootstrapVue)
// Optionally install the BootstrapVue icon components plugin
Vue.use(IconsPlugin)
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import './assets/vendor/fonts/boxicons.css'
import './assets/vendor/css/core.css'
import './assets/vendor/css/theme-default.css'
import './assets/vendor/css/pages/page-auth.css'
import './assets/css/demo.css'
import './assets/css/style.css'

import Paginate from 'vuejs-paginate'

Vue.component('paginate', Paginate)
import VueNumeric from 'vue-numeric'

Vue.use(VueNumeric)

import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';

Vue.use(DatePicker)


Vue.prototype.$http = Axios;
const token = localStorage.getItem('token')
if (token) {
    Vue.prototype.$http.defaults.headers.common['Authorization'] = 'Bearer ' + token
}
Vue.prototype.$http.defaults.headers.common['Access-Control-Allow-Origin'] = '*'
//Vue.prototype.$http.defaults.headers.common['Content-type'] = 'application/json'

const lang = localStorage.getItem('lang');

if (lang) {
    Vue.prototype.$http.defaults.headers.common['language'] = lang;
}
const currency = localStorage.getItem('currency');

if (currency) {
    Vue.prototype.$http.defaults.headers.common['currency'] = currency;
}
import VueGallery from 'vue-gallery';

Vue.use(VueGallery)
Vue.component('gallery', VueGallery)

import JsonCSV from 'vue-json-csv'

Vue.component('downloadCsv', JsonCSV)

import i18n from './components/mixins/i18n.vue';

Vue.mixin(i18n);
import Helpers from './components/mixins/Helpers.vue';

Vue.mixin(Helpers);

import vSelect from "vue-select";

Vue.component("v-select", vSelect);
import "vue-select/dist/vue-select.css";
import CKEditor from '@ckeditor/ckeditor5-vue2';
Vue.use( CKEditor );
import Datepicker from 'vuejs-datepicker';
Vue.use( Datepicker );
Vue.component("vuejs-datepicker", Datepicker);

// VuexChart

import VueApexCharts from 'vue-apexcharts'
import axios from "axios";
Vue.use(VueApexCharts)

Vue.component('apexchart', VueApexCharts)



Vue.component('sidebar', require('./components/Sidebar.vue').default);
Vue.component('topBar', require('./components/TopBar.vue').default);
Vue.component('modal', require('./components/Modal.vue').default);
Vue.component('personal-information', require('./components/Auth/PersonalInformation').default);
// import Pusher from "pusher-js";
// import axios from "axios";
new Vue({
    render: h => h(App),
    router,
    store,
    data: function () {
        return {
            api_url: process.env.VUE_APP_API_URL,
            app_name: process.env.VUE_APP_NAME,
            global_settings: {},

        }
    },
    methods:{
        get_global_settings() {
            axios({
                url: this.$root.$data.api_url + '/api/show-config',
                method: 'GET'
            })
                .then(resp => {
                    if (resp.data.error_code === 0) {
                        this.global_settings = resp.data.results;
                    }
                    this.process_loading = false

                })
                .catch(err => {
                    console.log(err)
                    this.process_loading = false
                })
        },

    },
    computed: {
        isMobile: function () {
            return this.$device.mobile;
        },
        current_currency: function () {
             return this.$store.getters.currency;
        },
        current_language: function () {
            return this.$store.getters.lang;
        },
        isLoggedIn:function () {
            return this.$store.getters.isLoggedIn
        },
    },
    created() {
        Axios.interceptors.response.use((response) => {
            return response;
        }, (error) => {
            if (error.response) {
                if (error.response.status === 401) {
                    // let currentUrl = window.location
                    // let domain = window.location.origin;
                    this.$store.dispatch('logout').catch(()=>{});
                   this.$router.push('/login').catch(()=>{});
                   //  window.location.replace(domain+'/login?return_url='+currentUrl);
                }
            }
            throw  error;
        })
        this.get_global_settings()
    },
}).$mount('#app')
