import Vue from 'vue'
import Router from 'vue-router'
import Login from './components/Auth/Login.vue'
import Register from './components/Auth/Register.vue'
import ForgotPassword from './components/Auth/ForgotPassword.vue'
import ResetPassword from './components/Auth/ResetPassword.vue'
import Dashboard from './components/Dashboard.vue'
import Profile from './components/Profile.vue'



import store from './store'
import NProgress from 'nprogress'

Vue.use(NProgress)
import 'nprogress/nprogress.css'

Vue.use(Router)

let router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'dashboard',
            component: Dashboard,
            meta: {
                requiresAuth: true,
                title: process.env.VUE_APP_NAME+' - '+process.env.VUE_APP_SLUG,
                metaTags: []
            }
        },
        {
            path: '/login',
            name: 'login',
            component: Login,
            meta: {
                requiresAuth: false,
                title: 'Login - '+process.env.VUE_APP_NAME,
                metaTags: []
            }
        },
        {
            path: '/register',
            name: 'register',
            component: Register,
            meta: {
                requiresAuth: false,
                title: 'Register - '+process.env.VUE_APP_NAME,
                metaTags: []
            }
        },
        {
            path: '/forgot-password',
            name: 'forgot-password',
            component: ForgotPassword,

            meta: {
                requiresAuth: false,
                title: 'Forgot password - '+process.env.VUE_APP_NAME,
                metaTags: [ ]
            }
        },
        {
            path: '/reset-password/:token/:email',
            name: 'reset-password',
            component: ResetPassword,
            props:true,
            meta: {
                requiresAuth: false,
                title: 'Rest password - '+process.env.VUE_APP_NAME,
                metaTags: [ ]
            }
        },
        {
            path: '/profile',
            name: 'profile',
            component: Profile,
            meta: {
                requiresAuth: true,
                title: 'Profile - '+process.env.VUE_APP_NAME,
                metaTags: [ ]
            }
        },
        {
            path: '/deposit/transaction',
            name: 'deposit-transaction',
            component: () => import("./components/Deposit"),
            meta: {
                requiresAuth: true,
                title: 'Deposit Transaction - '+process.env.VUE_APP_NAME,
                metaTags: [ ]
            }
        },
        {
            path: '/balance/history',
            name: 'balance-history',
            component: () => import("./components/BalanceHistory"),
            meta: {
                requiresAuth: true,
                title: 'Balance History - '+process.env.VUE_APP_NAME,
                metaTags: [ ]
            }
        },
        {
            path: '/bet/history',
            name: 'bet-history',
            component: () => import("./components/BetHistory"),
            meta: {
                requiresAuth: true,
                title: 'Bet History - '+process.env.VUE_APP_NAME,
                metaTags: [ ]
            }
        },
        {
            path: '/withdraw/account',
            name: 'withdraw-account',
            component: () => import("./components/WithdrawAccount"),
            meta: {
                requiresAuth: true,
                title: 'Withdraw Account - '+process.env.VUE_APP_NAME,
                metaTags: [ ]
            }
        },
        {
            path: '/withdraw',
            name: 'withdraw',
            component: () => import("./components/Withdraw"),
            meta: {
                requiresAuth: true,
                title: 'Withdraw - '+process.env.VUE_APP_NAME,
                metaTags: [ ]
            }
        },
        {
            path: '/payout',
            name: 'payout',
            component: () => import("./components/Payout"),
            meta: {
                requiresAuth: true,
                title: 'Payout - '+process.env.VUE_APP_NAME,
                metaTags: [ ]
            }
        },
        {
            path: '/topup',
            name: 'topup',
            component: () => import("./components/TopUp"),
            meta: {
                requiresAuth: true,
                title: 'Payout - '+process.env.VUE_APP_NAME,
                metaTags: [ ]
            }
        },
        {
            path: "*",
            component: () => import("./components/NotFound.vue"),
            meta: {
                title: 'Not Found',
                metaTags: []
            }
        },

    ]
})

export default router

router.beforeEach((to, from, next) => {


    // This goes through the matched routes from last to first, finding the closest route with a title.
    // eg. if we have /some/deep/nested/route and /some, /deep, and /nested have titles, nested's will be chosen.
    const nearestWithTitle = to.matched.slice().reverse().find(r => r.meta && r.meta.title);

    // Find the nearest route element with meta tags.
    const nearestWithMeta = to.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);

    // If a route with a title was found, set the document (page) title to that value.
    if (nearestWithTitle) document.title = nearestWithTitle.meta.title;

    // Remove any stale meta tags from the document using the key attribute we set below.
    Array.from(document.querySelectorAll('[data-vue-router-controlled]')).map(el => el.parentNode.removeChild(el));

    // Turn the meta tag definitions into actual elements in the head.
    nearestWithMeta.meta.metaTags.map(tagDef => {
        const tag = document.createElement('meta');

        Object.keys(tagDef).forEach(key => {
            tag.setAttribute(key, tagDef[key]);
        });

        // We use this to track which meta tags we create, so we don't interfere with other ones.
        tag.setAttribute('data-vue-router-controlled', '');

        return tag;
    }).forEach(tag => document.head.appendChild(tag));

    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (store.getters.isLoggedIn) {
            next()
            return
        }
        next('/login')
    } else {
        next()
    }
})

router.beforeResolve((to, from, next) => {
    if (to.name) {
        NProgress.start()
    }
    next()
})

router.afterEach(() => {
    NProgress.done();
    const el = document.getElementById('main-content');
    if(el){
        el.classList.remove('open-menu');
    }

});