<template>
  <div class="card">
    <div class="card-body">
      <h4 class="mb-2">{{ $trans('Sign in') }}</h4>
      <p class="mb-4">{{ $trans('Please sign-in to your account and start the adventure') }}</p>
      <form class="login mb-3" @submit.prevent="login">
        <div v-if="alert" class="alert" v-bind:class="'alert-'+alert.type">
          {{ alert.message }}
        </div>
        <div class="form-group">
          <label class="form-label">{{ $trans('Username') }}</label>
          <input class="form-control" autofocus v-model="username" type="text"/>
        </div>
        <div class="form-group">
          <div class="d-flex justify-content-between">
            <label class="form-label">{{ $trans('Password') }}</label>
          </div>
          <input class="form-control" required v-model="password" type="password"/>
        </div>
        <div class="d-flex justify-content-between">
            <button class="btn btn-primary d-grid" type="submit">{{ $trans('Sign in') }}</button>
            <span>Bạn chưa có tài khoản ? <router-link class=" d-grid" to="/register">{{ $trans('Register') }}</router-link></span>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data: function () {
    return {
      email: '',
      password: '',
      alert: null,
      process: false,
      remember: false,
      username: '',
      return_url: this.$route.query.return_url,
      errors: {}
    }
  },
  methods: {
    login: function () {
      if (this.process == false) {
        this.process = true;
        this.alert = null;
        let remember = this.remember
        let password = this.password
        let username = this.username
        this.$store.dispatch('login', {username, password, remember})
            .then(() => {
              this.process = false;
              if (this.return_url) {
                window.location.replace(this.return_url);
              } else {
                this.$router.push('/').catch(()=>{});
              }

            })
            .catch(err => {
              this.process = false;
              this.errors = {};
              this.alert = {
                type: 'warning',
                message: err.message
              }
            })
      }

    }
  },
  created() {
    if (this.$root.isLoggedIn) {
        this.$router.push('/').catch(()=>{});
    }
  }
}
</script>

