<template>
    <div>
        <nav class="layout-navbar container-xxl navbar navbar-expand-xl navbar-detached align-items-center bg-navbar-theme">
            <div class="layout-menu-toggle navbar-nav align-items-xl-center me-3 me-xl-0 d-xl-none">
                <a @click="openNav" class="nav-item nav-link px-0 me-xl-4" href="javascript:void(0)">
                    <i class="bx bx-menu bx-sm"></i>
                </a>
            </div>
            <div class="app-brand demo navbar-nav flex-row align-items-center ms-auto" >
                <router-link class="app-brand-link" to="/">
                        <span class="logo mr-1">
                             <img width="50" :src="this.$root.global_settings.logo_url"/>
                        </span>
                </router-link>
            </div>
        </nav>
    </div>

</template>

<script>
export default {
    name: "TopBar",
    data: function () {
        return {
            showMenu: false,
            showSidebar: true,
            current_currency: this.$root.current_currency,
            languages: {
                vi: {
                    title: 'Tiếng việt',
                    flag: ' data:image/png;base64,'
                },
                en: {
                    title: 'English',
                    flag: 'data:image/png;base64,'
                },
                zh: {
                    title: '中文',
                    flag: 'data:image/png;base64,'
                },
            }
        }
    },
    methods: {
        logout: function () {
            this.$store.dispatch('logout')
                .then(() => {
                    this.$router.push('/login')
                })
        },
        toggleBodyClass(addRemoveClass, className) {
            const el = document.body;
            if (addRemoveClass === 'addClass') {
                el.classList.add(className);
            } else {
                el.classList.remove(className);
            }
        },
        toggleTagClass: function (addRemoveClass, tagId, className) {
            const el = document.getElementById(tagId);
            if (addRemoveClass === 'addClass') {
                el.classList.add(className);
            } else {
                el.classList.remove(className);
            }
        },
        openNav: function () {
            this.toggleTagClass('addClass', 'main-content', 'open-menu');
        },
        closeNav: function () {
            this.showSidebar = false
            this.toggleTagClass('remove', 'main-content', 'open-menu');
        },
        change_language: function (lang) {
            this.$store.dispatch('setLang', lang)
            this.showLanguages = false
        },
    },
    computed: {
        user: function () {
            return this.$store.getters.user
        },
        roles: function () {
            let roles = JSON.parse(localStorage.getItem('roles'))
            if (!roles) {
                return null;
            }
            return roles;
        },
        current_language: function () {
            return this.$root.current_language
        }
    }
}
</script>

