<template>
    <div class="info-player d-flex row">
        <div class="col-sm-6">
            <div class="card">
                <div class="card-body">
                    <h6>{{ $trans('Thay đổi mật khẩu') }}</h6>
                    <hr>
                    <div v-if="alert" class="alert" v-bind:class="'alert-'+alert.type">
                        {{ alert.message }}
                    </div>
                    <div class="form-group" v-if="!profile.nickname">
                        <label>{{ $trans('Họ và tên') }}</label>
                        <input class="form-control" type="text" v-model="profile.nickname" disabled>
                        <slot v-if="errors && errors.nickname">
                            <div class="text-danger" v-for="error in errors.nickname" v-bind:key="error">
                                <small>{{ error }}</small>
                            </div>
                        </slot>
                    </div>
                    <div class="form-group" v-if="!profile.username">
                        <label>{{ $trans('Tên đăng nhập') }}</label>
                        <input class="form-control" type="text" v-model="profile.username" disabled>
                        <slot v-if="errors && errors.username">
                            <div class="text-danger" v-for="error in errors.username" v-bind:key="error">
                                <small>{{ error }}</small>
                            </div>
                        </slot>
                    </div>
                    <div class="form-group" v-if="!profile.phone">
                        <label>{{ $trans('Số điện thoại') }}</label>
                        <input class="form-control" type="text" v-model="profile.phone" disabled>
                        <slot v-if="errors && errors.phone">
                            <div class="text-danger" v-for="error in errors.phone" v-bind:key="error">
                                <small>{{ error }}</small>
                            </div>
                        </slot>
                    </div>
                    <div class="form-group">
                        <label>{{ $trans('Mật khẩu mới') }}</label>
                        <input v-model="profile.password" type="password" class="form-control">
                        <slot v-if="errors && errors.password">
                            <div class="text-danger" v-for="error in errors.password" v-bind:key="error">
                                <small>{{ error }}</small>
                            </div>
                        </slot>
                    </div>
                    <button @click="save_profile" class="btn btn-primary">{{ $trans('Lưu') }}</button>
                </div>
            </div>
        </div>
        <div class="col-sm-6">
            <div class="card">
                <div class="card-body">
                    <h6>{{ $trans('Thông tin cá nhân') }}</h6>
                    <hr>
                    <div class="form-group info-items d-flex">
                        <span>Họ và tên : </span>
                        <strong> {{ player.nickname }}</strong>
                    </div>
                    <div class=" form-group info-items d-flex">
                        <span>Tên đăng nhập : </span>
                        <strong> {{ player.username }}</strong>
                    </div>
                    <div class="form-group info-items d-flex">
                        <span>Số điện thoại : </span>
                        <strong> {{ player.phone }}</strong>
                    </div>
                    <div class="form-group info-items d-flex">
                        <span>Mã giới thiệu : </span>
                        <strong> {{ player.ref_number }}</strong>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    name: "PersonalInformation",
    data: function () {
        return {
            player: {},
            profile: {
                nickname: '',
                username: '',
                phone: '',
                email: '',
                password: ''
            },
            process: false,
            alert: null,
            errors: {}
        }
    },
    methods: {
        get_info: function () {
            axios({url: this.$root.$data.api_url + '/api/player', method: 'GET'})
                .then(resp => {
                    this.player = resp.data.results;
                    this.profile.nickname = resp.data.results.nickname
                    this.profile.username = resp.data.results.username
                    if (resp.data.results.phone) {
                        this.profile.phone = resp.data.results.phone;
                    }
                    if (resp.data.results.email) {
                        this.profile.email = resp.data.results.email;
                    }

                })
                .catch(err => {
                    console.log(err)
                })
        },
        save_profile: function () {
            if (this.process == false) {
                this.process = true;
                this.errors = {}
                this.alert = null
                axios({url: this.$root.$data.api_url + '/api/player', data: this.profile, method: 'POST'})
                    .then(resp => {
                        this.process = false;
                        const user = resp.data.results
                        if (resp.data.error_code === 0) {
                            this.alert = {
                                type: 'success',
                                message: "Bạn đã thay đổi thông tin thành công."
                            }
                            this.$store.commit('update_profile', user.username)
                            this.get_info()
                        } else {
                            this.alert = {
                                type: 'warning',
                                message: resp.data.message
                            }
                            this.errors = resp.data.errors || {};

                        }
                    })
                    .catch(err => {
                        this.process = false;
                        console.log(err)
                    })
            }
        }
    },
    created() {
        this.get_info();
    }
}
</script>

