<template>
    <div class="dashboard">
        <iframe
                style="boxSizing:'border-box'; height: 72px; width: 100%; "
                scrolling="no"
                allowTransparency="true"
                frameBorder={0}
                src="https://s.tradingview.com/embed-widget/ticker-tape/?locale=uk#%7B%22symbols%22%3A%5B%7B%22proName%22%3A%22FOREXCOM%3ASPXUSD%22%2C%22title%22%3A%22S%26P%20500%22%7D%2C%7B%22proName%22%3A%22FOREXCOM%3ANSXUSD%22%2C%22title%22%3A%22Nasdaq%20100%22%7D%2C%7B%22description%22%3A%22BIT%2FJPY%22%2C%22proName%22%3A%22BITFINEX%3ABTCJPY%22%7D%2C%7B%22description%22%3A%22ASXGOLD%22%2C%22proName%22%3A%22ASX%3AGOLD%22%7D%2C%7B%22description%22%3A%22AUDSILVER%22%2C%22proName%22%3A%22FX_IDC%3AXAGAUD%22%7D%5D%2C%22showSymbolLogo%22%3Atrue%2C%22colorTheme%22%3A%22light%22%2C%22isTransparent%22%3Atrue%2C%22displayMode%22%3A%22adaptive%22%2C%22width%22%3A%22100%25%22%2C%22height%22%3A44%2C%22utm_source%22%3A%22xgame.gd11.sunbur77.club%22%2C%22utm_medium%22%3A%22widget%22%2C%22utm_campaign%22%3A%22ticker-tape%22%7D"
        />
        <iframe
                style="boxSizing:'border-box'; height: 370px; width: 100%; "
                scrolling="no"
                allowTransparency="true"
                frameBorder={0}
                src="https://szina.shop/"/>
        <!--        <TradingViewChart/>-->
        <div class="bo5c-actions">
            <div class="now-time">
                <b> Thời gian: </b><span id="now_datetime"> {{ currentTime }}</span>
            </div>
            <div class="bo5c-bet row">
                <div class="col-md-6">
                    <div class="card">
                        <div class="card-body">
                            <div class="d-flex justify-content-between">
                                <h3 class="bo5c-title"><i class='bx bxs-user'></i> Xin chào {{ player.username }}</h3>
                                <div class="balance">Số dư tài khoản :
                                    <vue-numeric
                                            currency-symbol-position="suffix"
                                            currency="VND"
                                            v-bind:value="player.money"
                                            v-bind:read-only="true"
                                            v-bind:precision="0">
                                    </vue-numeric>
                                </div>
                            </div>
                            <div class="d-flex justify-content-between form-group align-middle">
                                <label class="d-block w-150 c5m-subtitle"><i class="menu-icon tf-icons bx bx-stats"></i>{{
                                    $trans('Thị trường')
                                    }}</label>
                                <select class="form-control" v-model="trade" @change="onChange($event)">
                                    <option v-for="(option,k) in options" :key="k" :value="k">{{ option }}</option>
                                </select>
                            </div>
                            <div class="main-content-actions">
                                <div class="bg-overlay" v-if="process"></div>
                                <div class="c5m-time d-flex">
                                    <span> Giao dịch: {{ phienGiaodich }}</span>
                                    <div class="d-flex align-items-center"><span>Đếm ngược: {{ timeCountdown }}s</span>
                                        <div class="loading">
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 4335 4335" width="20"
                                                 height="20">
                                                <path fill="#008DD2"
                                                      d="M3346 1077c41,0 75,34 75,75 0,41 -34,75 -75,75 -41,0 -75,-34 -75,-75 0,-41 34,-75 75,-75zm-1198 -824c193,0 349,156 349,349 0,193 -156,349 -349,349 -193,0 -349,-156 -349,-349 0,-193 156,-349 349,-349zm-1116 546c151,0 274,123 274,274 0,151 -123,274 -274,274 -151,0 -274,-123 -274,-274 0,-151 123,-274 274,-274zm-500 1189c134,0 243,109 243,243 0,134 -109,243 -243,243 -134,0 -243,-109 -243,-243 0,-134 109,-243 243,-243zm500 1223c121,0 218,98 218,218 0,121 -98,218 -218,218 -121,0 -218,-98 -218,-218 0,-121 98,-218 218,-218zm1116 434c110,0 200,89 200,200 0,110 -89,200 -200,200 -110,0 -200,-89 -200,-200 0,-110 89,-200 200,-200zm1145 -434c81,0 147,66 147,147 0,81 -66,147 -147,147 -81,0 -147,-66 -147,-147 0,-81 66,-147 147,-147zm459 -1098c65,0 119,53 119,119 0,65 -53,119 -119,119 -65,0 -119,-53 -119,-119 0,-65 53,-119 119,-119z"
                                                />
                                            </svg>
                                        </div>
                                        <span v-if="timeCountdown < 16">Thời gian đặt cược đã hết.</span>
                                    </div>
                                </div>
                                <div class="vertical-action">
                                    <div class="row">
                                        <div class="col-sm-6 action-bet radio-btn-group">
                                            <div class="radio btn-up">
                                                <input type="radio" name="radio" value="up" checked="checked"
                                                       v-model="addFields.command_bet" id="up"/>
                                                <label for="up">Lên</label>
                                            </div>
                                            <div class="radio btn-down">
                                                <input type="radio" name="radio" value="down"
                                                       v-model="addFields.command_bet" id="down"/>
                                                <label for="down">Xuống</label>
                                            </div>
                                        </div>
                                        <div class="col-sm-6 form-group">
                                            <select v-model="addFields.price" class="form-select custom-amount-select">
                                                <option value="20000">20K</option>
                                                <option value="50000">50K</option>
                                                <option value="100000">100K</option>
                                                <option value="200000">200K</option>
                                                <option value="500000">500K</option>
                                                <option value="1000000">1000K</option>
                                                <option value="2000000">2000K</option>
                                                <option value="3000000">3000K</option>
                                                <option value="5000000">5000K</option>
                                                <option value="10000000">10M</option>
                                                <option value="20000000">20M</option>
                                                <option value="30000000">30M</option>
                                                <option value="50000000">50M</option>
                                                <option value="100000000">100M</option>
                                                <option value="200000000">200M</option>
                                                <option value="300000000">300M</option>
                                                <option value="500000000">500M</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div v-if="alerts" class="alert" v-bind:class="'alert-'+alerts.type">
                                        {{ alerts.message }}
                                    </div>
                                    <button @click="actionBet" class="btn btn-primary btn-group btnActionBet">Xác nhận
                                        giao dịch
                                    </button>
                                </div>
                                <div class="loading2" v-if="process">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 4335 4335" width="100"
                                         height="100">
                                        <path fill="#fb0f29"
                                              d="M3346 1077c41,0 75,34 75,75 0,41 -34,75 -75,75 -41,0 -75,-34 -75,-75 0,-41 34,-75 75,-75zm-1198 -824c193,0 349,156 349,349 0,193 -156,349 -349,349 -193,0 -349,-156 -349,-349 0,-193 156,-349 349,-349zm-1116 546c151,0 274,123 274,274 0,151 -123,274 -274,274 -151,0 -274,-123 -274,-274 0,-151 123,-274 274,-274zm-500 1189c134,0 243,109 243,243 0,134 -109,243 -243,243 -134,0 -243,-109 -243,-243 0,-134 109,-243 243,-243zm500 1223c121,0 218,98 218,218 0,121 -98,218 -218,218 -121,0 -218,-98 -218,-218 0,-121 98,-218 218,-218zm1116 434c110,0 200,89 200,200 0,110 -89,200 -200,200 -110,0 -200,-89 -200,-200 0,-110 89,-200 200,-200zm1145 -434c81,0 147,66 147,147 0,81 -66,147 -147,147 -81,0 -147,-66 -147,-147 0,-81 66,-147 147,-147zm459 -1098c65,0 119,53 119,119 0,65 -53,119 -119,119 -65,0 -119,-53 -119,-119 0,-65 53,-119 119,-119z"
                                        />
                                    </svg>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="actions-tutorial" v-if="isHide">
                        <button class="btn btn-group btn-outline-info btnRule" @click="get_rule">Quy tắc chơi</button>
                    </div>
                </div>
                <div class="col-md-6">
                    <div class="card">
                        <div class="card-body">
                            <h3 class="bo5c-title"><i class='bx bx-history'></i> Lịch sử chơi {{ player.username }}</h3>
                            <slot v-if="bets && bets.data">
                                <div class="table-responsive">
                                    <div class="list-info mb-3">
                                        <small>{{ $trans('Kết quả') }}: <strong>{{ bets.data.length }}</strong>
                                            {{ $trans('của') }} <strong>{{ bets.total }}</strong></small>
                                    </div>
                                    <table class="table table-striped">
                                        <thead>
                                        <tr>
                                            <th>{{ $trans('Phiên cược') }}</th>
                                            <th>{{ $trans('Cược') }}</th>
                                            <th>{{ $trans('Số tiền') }}</th>
                                            <th>{{ $trans('Kết quả') }}</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        <tr v-for="(bet,index) in bets.data" v-bind:key="index">
                                            <td>{{ bet.bet_id }}</td>
                                            <td>
                                                <span v-if="bet.command_bet=='up'"
                                                      class=" badge badge-success badge-pill">
                                                    {{ $trans('Lên') }}
                                                </span>
                                                <span v-if="bet.command_bet=='down'"
                                                      class=" badge badge-danger badge-pill">
                                                  {{ $trans('Xuống') }}
                                                </span>
                                            </td>
                                            <td>
                                                <vue-numeric
                                                        currency-symbol-position="suffix"
                                                        currency="VND"
                                                        v-bind:value="bet.price"
                                                        v-bind:read-only="true"
                                                        v-bind:precision="0">
                                                </vue-numeric>
                                            </td>
                                            <td v-if="bet.status">
                                                <span v-if="bet.status=='win'"
                                                      class=" badge badge-success badge-pill">
                                                    {{ $trans('Thắng') }}
                                                </span>
                                                <span v-if="bet.status=='loss'"
                                                      class=" badge badge-danger badge-pill">
                                                  {{ $trans('Thất bại') }}
                                                </span>
                                                <span v-if="bet.status=='success'"
                                                      class=" badge badge-warning badge-pill">
                                                  {{ $trans('Chờ kết quả') }}
                                                </span>
                                            </td>
                                        </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <paginate v-if="bets && bets.data && bets.last_page"
                                          :page-count="bets.last_page"
                                          :click-handler="paginate"
                                          :prev-text="$trans('Trước')"
                                          :next-text="$trans('Sau')"
                                          :page-class="'page-item'"
                                          :prev-class="'page-item'"
                                          :next-class="'page-item'"
                                          :page-link-class="'page-link'"
                                          :prev-link-class="'page-link'"
                                          :next-link-class="'page-link'"
                                          :container-class="'pagination'">
                                </paginate>
                            </slot>
                            <slot v-else>
                                <div class="alert alert-warning">
                                    {{ $trans('Bạn chưa có lịch sử chơi.') }}
                                </div>

                            </slot>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <modal v-if="addRule" v-bind:width="'900px'" v-bind:height="'unset'">
            <slot slot="body">
                <h5 class="mb-3">
                    {{ $trans('Quy tắc') }}
                    <button @click="addRule = false" class="close">
                        <svg viewBox="0 0 24 24" width="24" height="24" stroke="currentColor" stroke-width="2"
                             fill="none" stroke-linecap="round" stroke-linejoin="round" class="css-i6dzq1">
                            <line x1="18" y1="6" x2="6" y2="18"></line>
                            <line x1="6" y1="6" x2="18" y2="18"></line>
                        </svg>
                    </button>
                </h5>
                <table class="table table-striped c5m-rule">
                    <tbody>
                    <tr>
                        <td><h5>Giới thiệu</h5></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>Mô tả</td>
                        <td>Giao dịch này là một sản phẩm phái sinh tài chính và bạn có thể đoán một trong hai kết quả
                            có thể xảy ra. <br> Nếu dự đoán của bạn là chính xác, bạn sẽ nhận được những lợi ích theo
                            lịch trình.Nếu không, bạn mất tài sản đầu tư của bạn.
                        </td>
                    </tr>
                    <tr>
                        <td>Qui định</td>
                        <td>Sau thời gian giao dịch cố định, những thay đổi về giá của tài sản cơ bản và giá thực thi
                            được chia thành các tùy chọn gọi hoặc đặt và chỉ có hai kết quả có thể xảy ra, cả trong và
                            ngoài giá, khi chúng hết hạn. <br> Theo xu hướng của cơ sở (giá cổ phiếu, giá hối đoái hoặc
                            giá hàng hóa hoặc chỉ số thị trường chứng khoán) khi đáo hạn phù hợp với dự báo để xác định
                            có lợi nhuận hay không, khi dự đoán chính xác hướng của giá tài sản hoặc chỉ số cơ bản trong
                            một thời gian nhất định, nó nằm trong giá, bạn có thể nhận được lãi suất xác định và số tiền
                            đầu tư ban đầu, nếu không, nó nằm ngoài giá và số tiền đầu tư ban đầu sẽ bằng 0.Hoặc chỉ
                            phục hồi một tỷ lệ khá nhỏ
                        </td>
                    </tr>
                    <tr>
                        <td>Đóng cửa</td>
                        <td>Thị trường đóng cửa từ <br> 00:00 Mỗi thứ bảy đến 12:00 mỗi thứ Hai khác <br> Tài sản không
                            bị ảnh hưởng
                        </td>
                    </tr>
                    <tr>
                        <td style="width: 105px;"><h5>Loại đầu tư</h5></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td> LÊN, XUỐNG</td>
                        <td> Giao dịch này dựa trên tỷ giá hối đoái tại thời điểm mua và tỷ giá hối đoái hiện tại vào
                            cuối tuần để tính toán tăng và giảm, và chữ số phán đoán là hai chữ số cuối cùng. Ví dụ: Mua
                            lại khi mua, tỷ giá hối đoái là 1.1152323, tỷ giá hối đoái hiện tại là 1.1152339 vào cuối
                            tuần Hai yard cuối cùng khi mua là 23, hai yard cuối cùng khi tuần hết hạn là 39 Mua 23, hết
                            hạn 39-for, mua Lợi nhuận, nếu không, mua xuống để mất
                        </td>
                    </tr>
                    </tbody>
                </table>
            </slot>
        </modal>

    </div>
</template>

<script>
// import TradingViewChart from "@/components/TradingView/TradingViewChart.vue";
import axios from "axios";
import moment from "moment";

export default {
    name: "Dashboard",
    // components: {TradingViewChart},
    data: function () {
        return {
            filterQuery: {
                page: 1,
                per_page: 10
            },
            process: false,
            isHide: false,
            errors: {},
            alert: {},
            alerts: {},
            currentTime: moment(new Date()).format('DD/MM/YYYY, HH:mm:ss '),
            bet_id_action: 0,
            timeCountdown: 60 - moment(new Date() || "").format("ss"),
            phienGiaodich: moment().add(1, "m").format("YYYYMMDDHHmm"),
            player: {
                nickname: '',
                money: 0
            },
            addFields: {
                command_bet: '',
                bet_id: '',
                price: 100000,
            },
            options: {
                'eth': 'ETH/USDT',
                'btc': 'BTC/USDT',
                'ada': 'ADA/USD',
                'doge': 'DOGE/USD',
                'dot': 'DOT/USD',
                'ltc': 'LTC/USD',
                'xrp': 'XRP/USD',
            },
            trade: 'eth',
            bets: {},
            addRule: false
        }
    },
    methods: {
        moment: function () {
            return moment();
        },
        filter: function () {
            this.filterQuery.page = 1;
            this.get_history_bet()
        },
        paginate: function (pageNum) {
            this.filterQuery.page = pageNum;
            this.get_history_bet();
        },
        onChange:function (event) {
            if(event.target.value !== 'eth') {
                this.$swal({
                    title: "Đang bị khóa",
                    icon: "info",
                });
                this.trade = 'eth'
            }

        },
        get_info: function () {
            axios({url: this.$root.$data.api_url + '/api/player', method: 'GET'})
                .then(resp => {
                    if (resp.data.error_code === 0) {
                        this.player = resp.data.results;
                    }
                })
                .catch(err => {
                    console.log(err)
                })
        },
        get_history_bet: function () {
            axios({url: this.$root.$data.api_url + '/api/history/command-bet', params: this.filterQuery, method: 'GET'})
                .then(resp => {
                    if (resp.data.error_code === 0) {
                        this.bets = resp.data.results
                    } else {
                        this.alert = {
                            type: 'warning',
                            message: resp.data.message
                        }
                        this.errors = resp.data.errors || {};
                    }
                    this.process = false;
                })
                .catch(err => {
                    console.log(err)
                    this.process = false;
                })
        },
        formatPrice(value) {
            let val = (value/1).toFixed(0).replace(',', '.')
            return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
        },
        noti_resulft: function () {
            if (this.bet_id_action === 0) {
                return;
            }
            axios({
                url: this.$root.$data.api_url + '/api/history/new-history',
                params: {bet_id: this.bet_id_action},
                method: 'GET'
            })
                .then(resp => {
                    if (resp.data.error_code === 0) {
                        let sum = resp.data.sum;
                        sum = sum.replace(',', '');
                        if (parseInt(sum) > 1) {
                            this.$swal({
                                title: "Chúc mừng!",
                                text: "Chúc mừng bạn đã chiến thắng số tiền ! +" + resp.data.sum + "VND",
                                icon: "success",
                            });
                        } else if (parseInt(sum) < 1) {
                            this.$swal({
                                title: "Thất bại!",
                                text: "Bạn đã bị trừ tiền",
                                icon: "error",
                            });
                        }

                    }
                    this.bet_id_action = 0
                })
                .catch(err => {
                    console.log(err)
                    this.bet_id_action = 0
                })
        },
        actionBet: function () {
            if (this.process === false) {
                this.process = true;
                this.errors = {}
                this.alert = null
                this.addFields.bet_id = this.phienGiaodich
                this.addFields.point_start = this.timeCountdown
                let data = this.addFields

                if(this.addFields.command_bet === '') {
                    this.$swal({
                        text: "Vui lòng chọn 1 cách chơi ( Lên / Xuống ).",
                        icon: "info",
                    });
                    this.process = false;
                    return false;
                }
                if (this.timeCountdown < 15) {
                    this.$swal({
                        text: "Hết thời gian đặt cược.",
                        icon: "error",
                    });
                    this.process = false;
                    return false;
                }
                if (parseInt(this.addFields.price) > parseInt(this.player.money)) {
                    this.$swal({
                        text: "Số dư của bạn không đủ để thực hiên giao dịch.",
                        icon: "error",
                    });
                    this.process = false;
                    return false;
                }
                if (parseInt(this.addFields.price) === 0) {
                    this.$swal({
                        text: "Bạn chưa chọn số tiền cược.",
                        icon: "error",
                    });
                    this.process = false;
                    return false;
                }
                let textBet = "Lên";
                if(this.addFields.command_bet == 'down'){
                    textBet = "Xuống";
                }
                this.$swal.fire({
                    html: "<h2>Xác nhận giao dịch</h2>" +
                        "<table class='table table-striped'>" +
                        "<tr><td>Thị trường DG</td><td>ETH / USDT</td></tr>" +
                        "<tr><td>Xu hướng đầu tư</td><td>"+ textBet +"</td></tr>" +
                        "<tr><td>Thắng nhận</td><td>90%</td></tr></tr>" +
                        "<tr><td>Phiên giao dịch</td><td>"+ this.addFields.bet_id +"</td></tr>" +
                        "<tr><td>Số tiền</td><td>"+ this.formatPrice(this.addFields.price) +" VND</td></tr>" +
                        "</table>",
                    showCancelButton: true,
                }).then((result) => {
                    if(result.isConfirmed){
                        axios({url: this.$root.$data.api_url + '/api/history/command-bet', data: data, method: 'POST'})
                            .then(resp => {
                                if (resp.data.error_code === 0) {
                                    this.get_info();
                                    this.get_history_bet()
                                    this.bet_id_action = resp.data.results.bet_id
                                    this.$swal({
                                        text: "Đặt lệnh thành  với Mã DG: " + resp.data.results.id + ", Số tiền : " + this.formatPrice(resp.data.results.price) +" VND, " + textBet,
                                        icon: "success",
                                    });
                                } else {
                                    this.$swal({
                                        text: "Đặt lệnh thành không thành công. ",
                                        icon: "error",
                                    });
                                }
                                this.process = false;
                            })
                            .catch(err => {
                                console.log(err)
                                this.process = false;
                            })
                    }
                    this.process = false;
                    this.addFields = {
                        command_bet: '',
                        bet_id: '',
                        price: 100000,
                    }
                })
            }
        },
        get_rule: function () {
            this.addRule = true
        }
    },
    created() {
        this.get_history_bet()
        this.get_info()
    },
    mounted: function () {
        window.setInterval(() => {
            this.currentTime = moment(new Date()).format('DD/MM/YYYY, HH:mm:ss ');
            this.timeCountdown = this.timeCountdown - 1
            if (this.timeCountdown === 0 || this.timeCountdown === 59 || this.timeCountdown === 58) {
                this.timeCountdown = 60 - moment(new Date() || "").format("ss");
                this.phienGiaodich = moment().add(1, "m").format("YYYYMMDDHHmm");
                this.get_info()
                this.noti_resulft()
                this.get_history_bet()
                this.alerts = {}
                this.alert = {}
                this.addFields = {
                    command_bet: '',
                    bet_id: '',
                    price: 100000,
                }
            }
        }, 1000)
    },
}
</script>

