
<script>
    export default {
        name: "Helpers",
        data:function () {
            return {
                permissions_of_user:JSON.parse(localStorage.getItem('permissions')),
                roles_of_user:JSON.parse(localStorage.getItem('roles')),
            }
        },
        methods:{
            $can:function (permission) {
                if( !permission || permission.length == 0){
                  return  true
                }
                let can =false;
                if( Array.isArray(permission)){
                    permission.forEach((p)=>{
                        if(this.permissions_of_user.indexOf(p) !== -1){
                            can = true;
                            return false;
                        }
                        //return this.permissions_of_user.indexOf(p) !== -1;
                    });
                }else{
                    if(this.permissions_of_user.indexOf(permission) !== -1){
                        can = true;
                    }
                }
               return can;
            },
            $get_precision:function (){
              let currency = this.$store.getters.currency
              let precision= 0
              switch (currency){
                case 'BTC':
                  precision = 7
                  break;
                default:
                  precision = 0
                  break;
              }
              return precision;
            },
          $get_current_currency:function (){
            return this.$store.getters.currency
          }
        }
    }
</script>