import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        status: '',
        token: localStorage.getItem('token') || '',
        user: localStorage.getItem('user') || '',
        lang: localStorage.getItem('lang') || 'vi',
        roles: localStorage.getItem('roles') || '{}',
        permissions: localStorage.getItem('permissions') || '{}',
        currency: localStorage.getItem('currency') || 'VND',
    },

    mutations: {
        auth_request(state) {
            state.status = 'loading'
        },
        auth_success(state, payload) {
            state.status = 'success'
            if (payload.token != '') {
                state.token = payload.token
            }
            if (payload.user) {
                state.user = payload.user
            }
            state.roles = payload.roles
            state.permissions = payload.permissions
        },
        auth_error(state) {
            state.status = 'error'
        },
        logout(state) {
            state.status = ''
            state.token = ''
        },
        update_profile(state, user) {
            state.status = 'success'
            state.user = user;
        },
        SET_LANG(state, payload) {
            state.lang = payload
        },
        SET_CURRENCY(state, payload) {
            state.currency = payload
        },

    },
    actions: {
        login({commit}, user) {
            return new Promise((resolve, reject) => {
                commit('auth_request')
                let data = user
                axios({url: process.env.VUE_APP_API_URL + '/api/login', data: data, method: 'POST'})
                    .then(resp => {

                        if (resp.status === 200) {
                            let token = resp.data.token
                            let user = resp.data.player
                            localStorage.setItem('token', token)
                            localStorage.setItem('user', user.username)
                            axios.defaults.headers.common['Authorization'] = 'Bearer ' + token
                            commit('auth_success', {
                                token: token,
                                user: user.username,
                            })
                            resolve(resp)
                        } else {
                            commit('auth_error')
                            localStorage.removeItem('token')
                            localStorage.removeItem('user')
                            localStorage.clear();
                            sessionStorage.clear();
                            reject(resp)
                        }
                    })
                    .catch(err => {
                        commit('auth_error')
                        localStorage.removeItem('token')
                        localStorage.removeItem('user')
                        localStorage.clear();
                        sessionStorage.clear();
                        reject(err)
                    })
            })
        },
        register({commit}, user) {
            return new Promise((resolve, reject) => {
                commit('auth_request')
                axios({url: process.env.VUE_APP_API_URL + '/api/register', data: user, method: 'POST'})
                    .then(resp => {
                        if (resp.status === 200) {
                            resolve(resp)
                        } else {
                            commit('auth_error')
                            localStorage.removeItem('token')
                            localStorage.removeItem('user')
                            reject(resp)
                        }
                    })
                    .catch(err => {
                        commit('auth_error', err)
                        localStorage.removeItem('token')
                        localStorage.removeItem('user')
                        reject(err)
                    })
            })
        },
        logout({commit}) {
            commit('auth_request')
            return new Promise((resolve) => {
                if(localStorage.getItem('token')){
                    location.reload()
                }
                localStorage.removeItem('token')
                localStorage.removeItem('user')
                localStorage.clear();
                sessionStorage.clear();
                delete axios.defaults.headers.common['Authorization']
                resolve()
            })
        },


        setLang({commit}, lang) {
            commit('SET_LANG', lang)
            localStorage.setItem('lang', lang)
            Vue.prototype.$http.defaults.headers.common['language'] = lang;
        },
        changeCurrency({commit}, currency) {
            commit('SET_CURRENCY', currency)
            localStorage.setItem('currency', currency)
            Vue.prototype.$http.defaults.headers.common['currency'] = currency;
            location.reload();
        }
    },
    getters: {
        isLoggedIn: state => !!state.token,
        authStatus: state => state.status,
        user: state => state.user,
        lang: state => state.lang,
        currency: state => state.currency,
    }
})
