<template>
    <div class="card">
        <div class="card-body register-form">
                <form class="register" @submit.prevent="register">
                    <h1>{{$trans('Register')}}</h1>
                    <div v-if="alert" class="alert" v-bind:class="'alert-'+alert.type">
                        {{alert.message}}
                    </div>
                    <div class="form-group">
                        <label>{{$trans('Nickname')}}</label>
                        <input class="form-control" v-model="nickname" type="text"/>
                        <slot v-if="errors && errors.nickname">
                            <div class="text-danger" v-for="error in errors.nickname" v-bind:key="error">
                                <small>{{ error }}</small>
                            </div>
                        </slot>
                    </div>
                    <div class="form-group">
                        <label>{{$trans('Phone')}}</label>
                        <input class="form-control" v-model="phone" type="text"/>
                        <slot v-if="errors && errors.phone">
                            <div class="text-danger" v-for="error in errors.phone" v-bind:key="error">
                                <small>{{ error }}</small>
                            </div>
                        </slot>
                    </div>
                    <div class="form-group">
                        <label>{{$trans('Referral')}}</label>
                        <input class="form-control" v-model="ref_number" type="text"/>
                        <slot v-if="errors && errors.ref_number">
                            <div class="text-danger" v-for="error in errors.ref_number" v-bind:key="error">
                                <small>{{ error }}</small>
                            </div>
                        </slot>
                    </div>
                    <div class="form-group">
                        <label>{{$trans('Username')}}</label>
                        <input class="form-control" v-model="username" type="text"/>
                        <slot v-if="errors && errors.username">
                            <div class="text-danger" v-for="error in errors.username" v-bind:key="error">
                                <small>{{ error }}</small>
                            </div>
                        </slot>
                    </div>
                    <div class="form-group">
                        <label>{{$trans('Password')}}</label>
                        <input class="form-control"  v-model="password" type="password" />
                        <slot v-if="errors && errors.password">
                            <div class="text-danger" v-for="error in errors.password" v-bind:key="error">
                                <small>{{ error }}</small>
                            </div>
                        </slot>
                    </div>
                    <div class="d-flex justify-content-between">
                        <button type="submit" class="btn btn-primary">
                            <div v-if="process" class="spinner-border spinner-border-sm text-secondary"></div>
                            {{$trans('Register')}}
                        </button>
                        <span>Bạn đã có tài khoản ? <router-link class="d-grid" to="/login">{{ $trans('Sign in') }}</router-link></span>
                    </div>

                </form>
        </div>
    </div>

</template>

<script>
    export default {
        name: "Register",
        data: function () {
            return {
                email: '',
                password: '',
                ref_number: '',
                nickname:'',
                username:'',
                phone:'',
                errors: {},
                alert: null,
                process: false
            }
        },
        methods: {
            register: function () {
                if (this.process == false) {
                    this.process = true;
                    let email = this.email
                    let password = this.password
                    let username = this.username
                    let ref_number = this.ref_number
                    let phone = this.phone
                    let nickname = this.nickname
                    this.alert = null;
                    this.errors ={}
                    this.$store.dispatch('register', {nickname,username, password, ref_number, phone, email})
                        .then((rep) => {
                            this.process = false;
                              this.alert = {
                                type: 'success',
                                message: "Tạo tài khoản "+ rep.data.results.username +" thành công."
                              }
                            setTimeout(()=>{
                                this.$router.push('/').catch(()=>{});
                            },5000);

                        })
                        .catch(err => {
                            this.process = false;
                            this.errors =  {};
                            this.alert = {
                                type: 'warning',
                                message: err.message
                            }
                        })
                }

            }
        }
    }
</script>

<style scoped>

</style>
