<script>
    export default {
        name: "i18n",
        data: function () {
            return {
                i18n: {
                    "vi": require('../../lang/vi.json'),
                    "en": require('../../lang/en.json'),
                    "zh": require('../../lang/zh.json'),
                    "th": require('../../lang/th.json'),
                }
            }
        },
        methods: {
            $trans: function (key, replace) {
                let _ = require('lodash');
                const lang = this.$store.getters.lang
                let translation;
                const i18n = this.i18n;
                if( !i18n[lang]){
                    return key;
                }
                translation = i18n[lang][key]
                    ? i18n[lang][key]
                    : key
                _.forEach(replace, (value, key) => {
                    translation = translation.replace(':' + key, value)
                })
                return translation
            }
        },
    }
</script>