<template>
    <div class="form forgot-password">
        <form class="login" @submit.prevent="reset_password">
            <h1>{{$trans('Forgot Your Password')}}</h1>
            <div v-if="alert" class="alert" v-bind:class="'alert-'+alert.type">
                {{alert.message}}
            </div>
            <div class="form-group">
                <label>{{$trans('Email')}}</label>
                <input class="form-control" required v-model="addFields.email" type="email"/>
            </div>
            <button class="btn btn-primary" type="submit">
                <div v-if="process" class="spinner-border spinner-border-sm text-secondary"></div>
                {{$trans('Send Password Reset Link')}}
            </button>
        </form>
    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: "ForgotPassword",
        data:function(){
            return{
                alert:null,
                process:false,
                errors:{},
                addFields:{
                    email:''
                }
            }
        },
        methods:{
            reset_password:function () {
                if (this.process == false) {
                    this.process = true;
                    this.errors = {}
                    this.alert = null
                    axios({url: this.$root.$data.api_url + '/api/auth/forgot-password', data: this.addFields, method: 'POST'})
                        .then(resp => {
                            let error_code = resp.data.error_code;
                            if (error_code == 0) {
                                this.alert = {
                                    type: 'success',
                                    message: resp.data.message
                                }
                            } else {
                                this.alert = {
                                    type: 'warning',
                                    message: resp.data.message
                                }
                                this.errors = resp.data.errors || {};
                            }
                            this.process = false;
                        })
                        .catch(err => {
                            console.log(err)
                            this.process = false;
                        })
                }
            }
        },
        created() {
            if( this.$root.isLoggedIn){
                this.$router.push('/').catch(()=>{});
            }
        }
    }
</script>

