<template>
    <aside id="layout-menu" class="layout-menu menu-vertical menu bg-menu-theme">
        <div class="app-brand demo">
            <router-link class="app-brand-link" to="/">
                <span class="logo mr-1">
                 <img width="100" :src="this.$root.global_settings.logo_url" />
                </span>
            </router-link>
            <a @click="closeNav" href="javascript:void(0);"
               class="layout-menu-toggle menu-link text-large ms-auto d-block d-xl-none">
                <i class="bx bx-chevron-left bx-sm align-middle"></i>
            </a>
        </div>
        <div class="menu-inner-shadow"></div>
        <ul class="menu-inner py-1">
            <li v-for="(menu,k) in main_menus" :key="k" class="menu-item"
                v-bind:class="{'open':menu.show,'active open':menuActive(menu.links)}">
                <slot v-if="$can(menu.permissions)">
                    <a v-if="menu.subs && menu.subs.length" @click="collapse(k)" href="javascript:void(0);"
                       class="menu-link menu-toggle">
                        <span v-html="menu.icon"></span>
                        <div>{{ $trans(menu.text) }}</div>
                    </a>
                    <slot v-else>
                        <router-link v-if="$can(menu.permissions)" class="menu-link" :to="menu.link">
                            <span v-html="menu.icon"></span>
                            <div>{{ $trans(menu.text) }}</div>
                        </router-link>
                    </slot>
                    <ul v-if="menu.subs && menu.subs.length" class="menu-sub">
                        <li v-for="(sub,i) in menu.subs" :key="i" class="menu-item"
                            v-bind:class="{'active':menuActive([sub.link])}">
                            <router-link v-if="$can(sub.permissions)" class="menu-link" :to="sub.link">
                                <div>{{ $trans(sub.text) }}</div>
                            </router-link>
                        </li>
                    </ul>
                </slot>
            </li>
            <li class="menu-item">
                <a  class="menu-link" v-bind:href="telegram_name" target="_blank">
                    <i class="menu-icon bx bxl-telegram"></i>
                    <span class="align-middle">{{$trans('Hỗ trợ')}}</span>
                </a>
            </li>
        </ul>
        <div class="btn-bottom menu-item">
            <a  @click="logout" href="javascript:void(0)">
                <i class="bx bx-power-off me-2"></i>
                <span class="align-middle">{{$trans('Đăng xuất')}}</span>
            </a>
        </div>
    </aside>
</template>
<script>
export default {
    name: "Sidebar",
    data: function () {
        return {
            app_name: process.env.VUE_APP_NAME,
            menus: [
                {
                    show: false,
                    link: '/',
                    text: 'Thị trường',
                    icon: '<i class="menu-icon tf-icons bx bx-stats"></i>',
                    permissions: [],
                    subs: []
                },
                {
                    show: false,
                    link: '/topup',
                    text: 'Nạp tiền',
                    icon: '<i class="menu-icon tf-icons bx bx-wallet-alt"></i>',
                    permissions: [],
                    subs: []
                },
                {
                    show: false,
                    link: '/withdraw',
                    text: 'Rút tiền',
                    icon: '<i class="menu-icon tf-icons bx bxs-credit-card"></i>',
                    permissions: [],
                    subs: []
                },
                // {
                //     show: false,
                //     link: '/bet/history',
                //     text: 'Lịch sử chơi',
                //     icon: '<i class="menu-icon tf-icons bx bx-list-check"></i>',
                //     permissions: [],
                //     subs: []
                // },
                {
                    show: false,
                    link: '/withdraw/account',
                    text: 'Thông tin tài khoản',
                    icon: '<i class="menu-icon tf-icons bx bxs-bank"></i>',
                    permissions: [],
                    subs: []
                },
                {
                    show: false,
                    link: '/profile',
                    text: 'Thông tin cá nhân',
                    icon: '<i class="menu-icon tf-icons bx bxs-user-detail"></i>',
                    permissions: [],
                    subs: []
                }
            ],
        }
    },
    methods: {
        logout: function () {
            this.$store.dispatch('logout')
                .then(() => {
                    this.$router.push('/login')
                })
        },
        collapse: function (key) {
            let menus = this.menus
            let new_menus = []
            menus.forEach((el, k) => {
                let item = el
                item.show = false
                if (k == key) {
                    item.show = !item.show
                    if (el.subs.length) {
                        let link = el.subs[0]['link']
                        this.$router.push(link)
                    }
                }
                new_menus.push(item)
            })
            this.menus = new_menus
        },
        menuActive: function (path) {
            if (path) {
                let routePath = this.$route.path;
                if (path.includes(routePath)) {
                    return true
                }
            }
            return false
        },
        toggleTagClass: function (addRemoveClass, tagId, className) {
            const el = document.getElementById(tagId);
            if (addRemoveClass === 'addClass') {
                el.classList.add(className);
            } else {
                el.classList.remove(className);
            }
        },
        openNav: function () {
            this.toggleTagClass('addClass', 'main-content', 'open-menu');
        },
        closeNav: function () {
            this.showSidebar = false
            this.toggleTagClass('remove', 'main-content', 'open-menu');
        },
    },
    created() {
        this.menuActive()
    },
    computed: {
        main_menus: function () {
            let menu = this.menus
            let main_menus = []
            menu.forEach((el) => {
                let item = el
                let links = []
                if (item.link != 'javascript:void(0)') {
                    links.push(item.link)
                }
                let main_permissions = el.permissions
                if (el.subs && el.subs.length) {
                    el.subs.forEach((sub) => {
                        links.push(sub.link)
                        let sub_permissions = sub.permissions
                        if (sub_permissions.length) {
                            sub_permissions.forEach((p) => {
                                main_permissions.push(p)
                            })
                        }
                    })
                }
                item.links = links
                item.permissions = main_permissions
                main_menus.push(item)
            })
            return main_menus;
        },
        telegram_name: function () {
            if(this.$root.global_settings.link_telegram){
                let username = this.$root.global_settings.link_telegram
                return 'https://t.me/' + username.replace('@','')
            }
            return 'https://t.me/username'
        },
    }
}
</script>

<style scoped>
.btn-bottom.menu-item {
    padding: 10px;
    position: fixed;
    bottom: 0;
    display: flex;
    width: 175px;
    background: #f4f5f6;
    justify-content: center;
    border-top: 2px #518aeb solid;
    justify-items: center;
}
</style>
